import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index.vue'
import login from '@/pages/login.vue'
import register from '@/pages/register.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/login/',
      name: 'login',
      component: login
    },
    {
      path: '/register/',
      name: 'register',
      component: register
    }
  ]
})



