<template>
  <mu-card class="login">
    <div>
      <mu-flexbox orient="vertical" align="center" style="text-align: center;">
        <mu-flexbox-item>
          <mu-text-field
            label="邮箱"
            type="email"
            icon="account_circle"
            v-model="email"
            v-bind:errorText="error_email"
            v-on:click="clear(this)"
            labelFloat/>
        </mu-flexbox-item>
        <mu-flexbox-item>
          <mu-text-field
            label="密码"
            labelFloat
            type="password"
            icon="https"
            v-bind:errorText="error_password"
            v-model="password"/>
        </mu-flexbox-item>
      </mu-flexbox>
    </div>
    <mu-card-actions>
      <mu-raised-button label="登录" v-on:click="login" primary/>
    </mu-card-actions>
  </mu-card>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        email: '',
        password: '',
        error_email: '',
        error_password: ''
      }
    },
    watch: {
      email: function () {
        if (this.email) {
          this.error_email = ''
        }
      },
      password: function () {
        if (this.password) {
          this.error_password = ''
        }
      }
    },
    methods: {
      login: function () {
        if (!this.email) {
          this.error_email = '用户名为空'
          return
        } else if (!this.password) {
          this.error_password = '密码为空'
          return
        }
        this.$http.post(
          api.login,
          JSON.stringify({
            'email': this.email,
            'password': this.password
          })).then((response) => {
            this.$cookie.set(
              'token',
              response.data.token,
              {expires: response.data.expiration.toString() + 's'}
            )
          }, (response) => {
            alert(response.data.error)
          })
      }
    }
  }
</script>

<style scoped>
  .login {
    margin: 8px;
  }
</style>



