<template>
  <mu-card :key="post.id" v-bind:id="post.post_id" class="article">
    <mu-card-header v-bind:title="post.author" v-bind:subTitle="post.timestamp" class="article-head">
      <mu-avatar src="/static/uicon.jpg" slot="avatar"/>
    </mu-card-header>
    <mu-card-title v-bind:title="post.title"/>
    <mu-card-text class="article-head">
      {{ post.body_html }}
    </mu-card-text>
    <mu-card-actions class="article-button">
      <mu-icon-button icon="favorite"/>
    </mu-card-actions>
  </mu-card>
</template>

<script>
  export default {
    props: {
      post: ''
    }
  }
</script>

<style scoped>
  .article-head {
    text-align: left;
  }
  .article-button {
    text-align: right;
  }
</style>



