<template>
  <div class="demo-grid">
    <mu-row gutter>
      <mu-col width="100" tablet="66" desktop="66">
        <moon-article v-for="post in posts" v-bind:post="post" :key="post.id"></moon-article>
      </mu-col>
      <mu-col width="100" tablet="33" desktop="33">
        <moon-slide></moon-slide>
      </mu-col>
    </mu-row>
  </div>
</template>

<script>
  import article from '../components/article.vue'
  import slide from '../components/slidebar.vue'
  import api from '../services/api'
  export default {
    data: function () {
      return {
        posts: [
          {
            'post_id': 1,
            'title': 'test',
            'body_html': '123123123',
            'timestamp': '2017.1.1',
            'author': 'lvcong'
          }
        ]
      }
    },
    mounted: function () {
      this.$http.get(api.get_posts).then((response) => {
        this.posts = response.data.posts
      })
    },
    components: {
      'moon-slide': slide,
      'moon-article': article
    },
    methods: {
      'Like': function () {
        alert(1)
      }
    }
  }
</script>

<style lang="css">
  .demo-grid div[class*="col-"] {
    margin-bottom: 50px;
    margin-top: 8px;
    padding: 8px;
  }
</style>




