<template>
  <mu-appbar>
    <mu-flat-button label="Blog" class="demo-flat-button" icon="home" slot="left" to="/" primary/>
    <mu-flat-button v-bind:label="name" slot="right"/>
    <mu-icon-menu icon="more_vert" slot="right">
      <mu-menu-item v-if="!name" title="login" href="#/login/"/>
      <mu-menu-item v-if="name" title="logout" @click="logout"/>
    </mu-icon-menu>
  </mu-appbar>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        title: 'blog',
        name: ''
      }
    },
    mounted: function () {
      this.$http.get(api.get_user_info).then((response) => {
        this.name = response.data.username
      }, (response) => {
        this.name = 'Hello'
      })
    },
    methods: {
      'Home': function () {
        alert('home')
      },
      logout: function () {
        this.$cookie.delete('token')
      }
    }
  }
</script>



